import React, { useState } from 'react';
import { Link } from "react-router-dom";

import './virtual.css'

export default function Virtual() {
    const [basic, setBasic] = useState(false);
    const _basic = basic ?
        <div>
            <br />
            <ul style={{ textAlign: "start" }}>
                <li>Domiciliacin fiscal, comercial y social.</li>
                <li>Recepcin de mensajera y paquetera.</li>
                <li>Aviso inmediato de mensajes urgentes y certificados.</li>
                <li>Aviso semanal de correspondencia ordinaria acumulada.</li>
                <li>Recepcin y atencin de visitas segn instrucciones.</li>
                <li>Utilizacin de la direccin en tarjetas y pgina web.</li>
                <li>Descuento en alquiler de espacios.</li>
                <li>Invitacin a eventos de la comunidad.</li>
            </ul>
        </div> : <></>

    const [premiun, setPremiun] = useState(false);
    const _premiun = premiun ?
        <div>
            <br />
            <ul style={{ textAlign: "start" }}>
                <li>Domiciliacin comercial, fiscal y/o social .</li>
                <li>Recepcin de mensajera y paquetera.</li>
                <li>Aviso inmediato de mensajes urgentes y certificados.</li>
                <li>Aviso semanal de correspondencia ordinaria acumulada.</li>
                <li>Recepcin y atencin de visitas siguiendo tus instrucciones.</li>
                <li>Atencin telefnica en nombre de tu empresa en espaol e ingls.</li>
                <li>Envo de avisos por email con los datos de las llamadas recibidas.</li>
                <li>Un da al mes de uso del Open Space.</li>
                <li>Descuento en alquiler de espacios.</li>
                <li>Acceso a eventos y actividades de la comunidad.</li>
            </ul>
        </div> : <></>
    return (
        <section id="virtual">
            <h1>Oficina Virtual</h1>
            <h5>Ubique su empresa en una direccin prestigiosa. Le ofrecemos la gestin de<br />
            correspondencia y atencin al cliente.
        </h5>
            <div className="container virtual">
                <div className="row">
                    <div className="col-md-6 col-12">
                        <div className="item">
                            <h4>Oficina Virtual Basic</h4>
                            <h1>$30</h1>
                            <h3>/ mes</h3>
                            <a onClick={() => { setBasic(!basic) }}>Ver servicios incluidos</a>
                            <br />
                            {_basic}
                            <button type="button" className="btn btn-get">Solicitar Informacin</button>
                        </div>
                    </div>
                    <div className="col-md-6 col-12">
                        <div className="item">
                            <h4>Oficina Virtual Premiun</h4>
                            <h1>$90</h1>
                            <h3>/ mes</h3>
                            <a onClick={() => { setPremiun(!premiun) }}>Ver servicios incluidos</a>
                            <br />
                            {_premiun}
                            <button type="button" className="btn btn-get">Solicitar Informacin</button>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    );
}